export default {
	data() {
		return {
			goodsName: '',
			historyList:[],
			currentHistoryTab:-1,
		};
	},
	mounted(){
		this.currentHistoryTab = -1
		this.goodsName = ''
		this.historyList = uni.getStorageSync('historyList')
	},
	methods:{
		bindNameInput(e){
			this.goodsName = e.target.value
			console.log(e.target.value)
		},
		clickSearch(){
			if(this.goodsName){
				const newHistoryList = this.historyList || []
				newHistoryList.unshift(this.goodsName)
				// this.$set(this.$data,'historyList', newHistoryList)
				this.historyList = this.unique(newHistoryList).slice(0,8)
				uni.setStorageSync('historyList', this.historyList)
			}
			uni.navigateTo({
				url: '/pages/train/searchResult'
			})
		},
		// 数组去重
		unique (arr) {
			return Array.from(new Set(arr))
		},
		clickDelete(){
			
		},
		clickHistoryTab(index){
			if (this.currentHistoryTab == index) {
				return false
			} else {
				this.currentHistoryTab = index
			}
		}
	}
}
